import goods from '../goods/goods.vue'
import { GoodInfo } from '../../shopCarType'

@Component({
	components: {
		goods
	}
})
export default class Unusable extends Vue {
	@Prop()
	type!: number | string
	@Prop()
	shopLoseCarList!: Array<GoodInfo>

	/**
	 * @LastEditors: chuyinlong
	 * @description: 清空失效商品
	 * @param {*}
	 * @returns {*}
	 */

	emptyUseless(): void {
		this.$emit('emptyUseless')
	}
}
